import {WebAPI} from './web-api';
import {inject} from 'aurelia-framework';
import {LogManager} from 'aurelia-framework';

let log = LogManager.getLogger('contact-list');

@inject(WebAPI)
export class ContactList {
  constructor(api) {
    this.api = api;
    this.contacts = [];
    this.activePage = 1;
    this.maxPage = 1;
    this.lineCount = 10;
    this.selectedRow = -1;
    this.onCreating = false;
  }

  showList() {
    this.api.getContactList(this.activePage, this.lineCount).then(contacts => {
      log.info('Success!');
      log.info(contacts);
      this.contacts = contacts;
      let allLineCount = contacts[0].LineCount;
      this.maxPage = Math.floor(allLineCount / this.lineCount);
      if (allLineCount % this.lineCount !== 0) {
        this.maxPage += 1;
      }
    }).catch(error => {
      log.info('Error!');
      log.info(error);
    });
  }

  created() {
    //this.api.getContactList().then(contacts => this.contacts = comntacts);
    this.onCreating = true;
    log.info('created!');
    this.showList();
  }

  onPageChanged(e) {
    log.info(`page changed ${e.detail}`);
    if (this.onCreating) {
      this.onCreating = false;
    } else {
      this.activePage = e.detail;
      this.showList();
    }
    this.selectedRow = -1;
  }

  onClickRow(idx) {
    this.selectedRow = idx;
  }
}
