import {WebAPI} from './web-api';
import {inject, NewInstance} from 'aurelia-framework';
import {LogManager} from 'aurelia-framework';
import { ValidationController, ValidationRules } from 'aurelia-validation';
import { MaterializeFormValidationRenderer } from 'aurelia-materialize-bridge';

let log = LogManager.getLogger('contact-list');

@inject(WebAPI, NewInstance.of(ValidationController))
export class ContactNew {
  rules = ValidationRules
    .ensure('contactName')
      .required()
        .withMessage('連絡先名称は必須です')
    .ensure('orderDisplay')
      .required()
        .withMessage('表示順は必須です')
      .matches(/[0-9]+/)
        .withMessage('数値を指定して下さい')
      .rules;

  constructor(api, controller) {
    this.api = api;
    this.controller = controller;
    this.controller.addRenderer(new MaterializeFormValidationRenderer());

    this.contactName = '';
    this.orderDisplay = '';
    this.note = '';
    this.activeFlg = true;
    log.info('ContactNew-constructor');
  }

  validateModel() {
    this.controller.validate().then(v => {
      if (v.valid) {
        this.message = 'All is good!';
      } else {
        this.message = 'You have errors!';
      }
    });
  }

  created() {
    log.info('ContactNew-created');
  }
}
